const Eventos = props => {
  return (
    <a className="list-group-item active text-light mb-1" href={props.evento[33]} target='_blank'>
      <h3 className="card-title">{props.evento[0].text}</h3>
      <p className="card-title">{props.evento[33]}</p>
    </a>
  );
};

export default Eventos;
